body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body.dark{
    background:#061629;
}

.container.dark{
    background:rgb(10, 31, 54);
    color:#4c7096!important;
    
}

.dark select:hover{
    background-color: #4c70963d!important;
    color:#4c7096!important;
}

.dark .form-check-input:checked {
    background-color: #4c70963d!important;
    color:#4c7096!important;
    border-color:#3e5873;
}
.dark select{
    
    color:#4c7096!important;
}

.text-dark, .dark input::placeholder{
    color:#4c7096!important;
}


::-webkit-datetime-edit { padding: 1em; }

::-webkit-datetime-edit-text,
::-webkit-datetime-edit-month-field,
::-webkit-datetime-edit-day-field,
::-webkit-datetime-edit-year-field, 
::-webkit-calendar-picker-indicator { color: #4c7096!important;}




.addinput.dark{
    background:rgb(13, 7, 42)!important;
    color:#4c7096!important;
    box-shadow: 0 .5rem 1rem rgba(250,250,255,.15)!important;
}

.add-todo-wrapper.dark{
    background-color: #4c70963d!important;
}

body.dark input[type="text"],.dark{
    color:#4c7096;
}

.darkbg{
    color:rgb(10, 31, 54);
    background-color:#4c7096;
}

.btn-dark{
    background-color:#4c709671;
    color: rgb(10, 31, 54);
    border-color:transparent;
}

.btn-dark:hover{
    background-color:#4c70968f;
    color: rgb(10, 31, 54);
    border-color:transparent;
    
}

.border-dark{
    border-color:#4c7096!important;
}

.add-todo-input,
.edit-todo-input {
    outline: none;
}

.add-todo-input:focus,
.edit-todo-input:focus {
    border: none !important;
    box-shadow: none !important;
}

.view-opt-label,
.date-label {
    font-size: 0.8rem;
}

.edit-todo-input {
    font-size: 1.7rem !important;
}

.todo-actions {
    visibility: hidden !important;
}

.todo-item:hover .todo-actions {
    visibility: visible !important;
}

.todo-item.editing .todo-actions .edit-icon {
    display: none !important;
}

.todo-item button, .todo-item button:focus{
    background:transparent;
    border:0;
    outline:none;
}



.editingOn{
    transition: 0.4s ease all;
    font-size: 15px!important;
    position: relative;
    border-bottom: 1px solid #666!important;
    border-radius: 0!important;
}


@keyframes editAnim{
    0%{width:0%}
    100%{width:100%}
}